.profile-btn {
  background-color: #fbfbfb;
  border-right: 1px solid #eaeaea;
  list-style: none;
}

.profile-btn__link {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  text-decoration: none;
  display: flex;
}

.profile-btn__wrap {
  width: 30px;
  height: 30px;
  background-color: #2e69fd;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

